// clear fix
@mixin clear-fix {
  &:after {
    display: block;
    content: "";
    clear: both; } }

// Text Replace
@mixin text-replace {
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap; }

// Inline Block
@mixin inline-block-parent {
  font-size: 0;
  letter-spacing: -1em;
  word-spacing: -1em; }

@mixin inline-block {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  vertical-align: top;
  font-size: 1.6rem;
  letter-spacing: normal;
  word-spacing: normal; }

// åºæ¬ã®æ¨ªä¸¦ã³ Navigation
@mixin navigation-list {
  @include inline-block-parent;
  margin: 0;
  padding: 0;
  list-style-type: none; }

@mixin navigation-list-item {
  @include inline-block;
  margin-left: 40px;

  &:first-child {
    margin-left: 0; } }

@mixin navigation-list-item-link {
  @include animation;
  color: $fontColor-inverse;
  font-family: $fontFamily-specific;
  font-size: 14px;
  letter-spacing: $letterSpacing-title; }


// Media Queries Settings
// Retina

@mixin mediaquery($size) {
  @media only screen and (max-width: $size) {
    @content; } }

@mixin mediaquery-retina {
  @media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min-resolution: 1.5dppx) {
    @content; } }

// PC
@mixin mediaquery-pc {
  @media only screen and (max-width: $breakpoint-pc) {
    @content; } }


// Tablet
@mixin mediaquery-tablet {
  @media only screen and (max-width: $breakpoint-tablet) {
    @content; } }

// SmartPhone
@mixin mediaquery-smartphone {
  @media only screen and (max-width: $breakpoint-smartphone) {
    @content; } }


// Extend
// CSS Transition Easing
// Default
$linear: cubic-bezier(0.250, 0.250, 0.750, 0.750);
$ease: cubic-bezier(0.250, 0.100, 0.250, 1.000);
$ease-in: cubic-bezier(0.420, 0.000, 1.000, 1.000);
$ease-out: cubic-bezier(0.000, 0.000, 0.580, 1.000);
$ease-in-out: cubic-bezier(0.420, 0.000, 0.580, 1.000);
// In
$easeInQuad: cubic-bezier(0.550, 0.085, 0.680, 0.530);
$easeInCubic: cubic-bezier(0.550, 0.055, 0.675, 0.190);
$easeInQuart: cubic-bezier(0.895, 0.030, 0.685, 0.220);
$easeInQuint: cubic-bezier(0.755, 0.050, 0.855, 0.060);
$easeInSine: cubic-bezier(0.470, 0.000, 0.745, 0.715);
$easeInExpo: cubic-bezier(0.950, 0.050, 0.795, 0.035);
$easeInCirc: cubic-bezier(0.600, 0.040, 0.980, 0.335);
$easeInBack: cubic-bezier(0.600, -0.280, 0.735, 0.045);
// Out
$easeOutQuad: cubic-bezier(0.250, 0.460, 0.450, 0.940);
$easeOutCubic: cubic-bezier(0.215, 0.610, 0.355, 1.000);
$easeOutQuart: cubic-bezier(0.165, 0.840, 0.440, 1.000);
$easeOutQuint: cubic-bezier(0.230, 1.000, 0.320, 1.000);
$easeOutSine: cubic-bezier(0.390, 0.575, 0.565, 1.000);
$easeOutExpo: cubic-bezier(0.190, 1.000, 0.220, 1.000);
$easeOutCirc: cubic-bezier(0.075, 0.820, 0.165, 1.000);
$easeOutBack: cubic-bezier(0.175, 0.885, 0.320, 1.275);
// In Out
$easeInOutQuad: cubic-bezier(0.455, 0.030, 0.515, 0.955);
$easeInOutCubic: cubic-bezier(0.645, 0.045, 0.355, 1.000);
$easeInOutQuart: cubic-bezier(0.770, 0.000, 0.175, 1.000);
$easeInOutQuint: cubic-bezier(0.860, 0.000, 0.070, 1.000);
$easeInOutSine: cubic-bezier(0.445, 0.050, 0.550, 0.950);
$easeInOutExpo: cubic-bezier(1.000, 0.000, 0.000, 1.000);
$easeInOutCirc: cubic-bezier(0.785, 0.135, 0.150, 0.860);
$easeInOutBack: cubic-bezier(0.680, -0.550, 0.265, 1.550);

// animation
@mixin animation {
  transition: {
    duration: 250ms;
    timing-function: $ease;
    property: all; } }
