* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  height: 100%;
  font-size: 58.5%; }

body {
  height: 100%;
  font-family: $fontFamily;
  font-weight: $fontWeight;
  background-color: $bgColor-base;
  color: $fontColor;
  font-size: $fontSize-base;
  line-height: 1.7; }

#container {
  position: relative;
  min-height: 100%; }

#main {
  padding-bottom: 90px; }

footer {
  width: 100%;
  height: 60px;
  position: absolute;
  bottom: 0; }

h1 {
  font-size: 2.8rem;
  font-weight: normal;
  line-height: 1.2;
  @include mediaquery-tablet {
    font-size: 2.2rem; } }


h2 {
  font-size: 2.4rem;
  font-weight: normal;
  line-height: 1.2;
  @include mediaquery-tablet {
    font-size: 2.0rem; } }

h3,h4 {
  font-weight: normal; }

a {
  @include animation;
  color: $fontColor;
  text-decoration: none; }

p {
  margin: 15px 0; }

ul {
  list-style: none inside; }

input {
  outline: 0; }

table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid $borderColor-gray;

  tr {
    border-bottom: 1px solid $borderColor-gray;
    &:last-child {
      border-bottom: 0; }
    &:nth-of-type(even) {
      background-color: $mainColor-moreLighter; } }

  th {
    text-align: left; }

  th,
  td {
    padding: 5px 10px; } }

hr {
  height: 0px;
  margin: 1rem 0 2rem 0;
  border: none;
  border-top: 1px solid $borderColor-gray; }

small {
  line-height: 1.0; }

.block {
  display: block; }

.hidden {
  display: none; }

.nowrap {
  white-space: nowrap; }

.emphasis {
  font-weight: bold;
  color: $blue;
  font-size: 2.0rem; }

.w-full {
  width: 100%; }

.inline-block {
  display: inline-block; }

.text-center {
  text-align: center; }

.text-blue {
  color: $blue; }

.text-red {
  color: $red; }

.text-link {
  color: $textLinkColor;
  text-decoration: underline;
  &:hover {
    text-decoration: none; } }

.error-text {
  margin-top: 5px;
  color: $fontColor-error; }

.pointer {
  cursor: pointer; }

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  appearance: none; }


// clearFix
.cf {
  content: "";
  display: block;
  clear: both; }


// margin
.mr0 {
  margin-right: 0; }


.mt5 {
  margin-top: 5px; }
.mb5 {
  margin-bottom: 5px; }
.ml5 {
  margin-left: 5px; }
.mr5 {
  margin-right: 5px; }


.mt10 {
  margin-top: 10px; }
.mb10 {
  margin-bottom: 10px; }
.ml10 {
  margin-left: 10px; }
.mr10 {
  margin-right: 10px; }


.mt1 {
  margin-top: 1rem; }
.mb1 {
  margin-bottom: 1rem; }
.ml1 {
  margin-left: 1rem; }
.mr1 {
  margin-right: 1rem; }

.mt2 {
  margin-top: 2rem; }
.mb2 {
  margin-bottom: 2rem; }
.ml2 {
  margin-left: 2rem; }
.mr2 {
  margin-right: 2rem; }

// mediaquery
.forSP {
  display: none;
  @include mediaquery-smartphone {
    display: block; } }

.forTablet {
  display: none;
  @include mediaquery-tablet {
    display: block; } }

.overSP {
  @include mediaquery-smartphone {
    display: none; } }

.overTablet {
  @include mediaquery-tablet {
    display: none; } }

.overPC {
  @include mediaquery-pc {
    display: none; } }


// font size
.text-normal {
  font-size: $fontSize-base; }

.text-sm {
  font-size: 1.4rem; }

.text-xs {
  font-size: 1.2rem; }

// accordion
.accordion-trigger {
  cursor: pointer; }

