.share {
  @include inline-block-parent;
  text-align: center;

  @include mediaquery-tablet {
    margin: 0 -15px;
    padding: 0 9%; } }


.share-item {
  @include inline-block;
  margin: 0 15px;

  @include mediaquery-pc {
    margin: 0 5px; }

  @include mediaquery-tablet {
    width: 49.5%;
    margin: 5px 0;
    padding: 0 5px; } }


.share-item--line {
  @extend.share-item;
  display: none;

  @include mediaquery-tablet {
    @include inline-block; } }

