.l-show {
  @include clear-fix; }

.l-show__main {
  float: left;
  max-width: $breakpoint-tablet;
  @include mediaquery-pc {
    float: none;
    margin: 0 auto; } }

.l-show__side {
  float: right;
  width: $breakpoint-pc - $breakpoint-tablet;
  padding-left: 2rem;
  @include mediaquery-pc {
    display: none; } }




