$gridMargin: 10px;

.l-container {
  max-width: 1130px;
  margin-right: auto;
  margin-left: auto;
  overflow: hidden;
  box-sizing: content-box;

  @include mediaquery-pc {
    margin-right: 10px;
    margin-left: 10px; } }


.l-container--pcFull {

  @extend.l-container;

  @include mediaquery-pc {
    margin-right: 0;
    margin-left: 0; } }


.l-row {
  @include inline-block-parent;
  margin-right: $gridMargin * -1;
  margin-left: $gridMargin * -1;
  box-sizing: content-box; }


[class*="l-grid-"] {
  @include inline-block;
  padding-left: $gridMargin;
  padding-right: $gridMargin;
  min-height: 1px; }


@for $i from 1 through 12 {
  .l-grid-#{$i} {
    width: (100% / 12) * $i; } }


.l-container--narrow {
  @extend.l-container;
  max-width: 786px;
  $gridMargin: 15px;


  .l-row {
    @include clear-fix;
    margin-right: $gridMargin * -1;
    margin-left: $gridMargin * -1; }


  [class*="l-grid-"] {
    padding-right: $gridMargin;
    padding-left: $gridMargin; } }


.l-container--full {
  max-width: 100%;
  padding: 0; }

