.m-show-side-section {

  .postList {
    display: block; }

  .postList__item {
    margin-bottom: 1rem;
    border: 1px solid $borderColor-lightgray;
    box-shadow: none; }

  .postList__link {
    display: grid;
    grid-template-columns: 150px 1fr;
    grid-template-rows: 2.2rem 1fr; }

  .postList__figure {
    grid-row-start: 1;
    grid-row-end: 3;
    margin: 0;
    margin-bottom: 1.25rem;
    overflow: hidden; }

  .postList__img {
    display: block;
    width: 100%;
    height: auto; }

  .postList__header,
  .postList__body,
  .postList__footer {
    padding: 0 1rem; }


  .postList__header {
    margin-bottom: .5rem;
    color: $fontColor-gray;
    font-size: 1.2rem; }

  .postList__subscriber {
    color: $blue;
    margin-right: 0.5rem; }

  .postList__date {
    letter-spacing: 1px; }


  .postList__body {
    margin-bottom: 1.5rem; }

  .postList__title {
    font-size: 2rem;
    font-size: 1.5rem;
    font-weight: normal; }

  .postList__footer {
    display: none; } }
