.button {
  padding: 5px 10px;
  color: $fontColor-inverse;
  background-color: $btnColor;
  border: 1px solid $btnColor;
  text-align: center;
  border-radius: 5px;
  outline: 0; }


.button__red {
  @extend .button;
  background-color: $red;
  border-color: $red; }


.button__inverse {
  @extend .button;
  color: $btnColor;
  background-color: $bgColor-base; }


.button__slim {
  @extend .button;
  padding: 1px 5px; }


.button__slimInverse {
  @extend .button__inverse;
  padding: 1px 5px; }


.button__facebook {
  @extend.button;
  color: $facebook;
  border-color: $facebook;

  &:hover {
    color: $fontColor-inverse;
    background-color: $facebook; } }


.button__twitter {
  @extend.button;
  color: $twitter;
  border-color: $twitter;

  &:hover {
    color: $fontColor-inverse;
    background-color: $twitter; } }


.button__hatena {
  @extend.button;
  color: $hatena;
  border-color: $hatena;

  &:hover {
    color: $fontColor-inverse;
    background-color: $hatena; } }

.button__line {
  @extend.button;
  color: $line;
  border-color: $line;

  &:hover {
    color: $fontColor-inverse;
    background-color: $line; } }

